import React, { Component } from 'react';
import Datum from "../data.js";

class TodayDescriptor extends Component {
  state = {
    now: new Date()
  }

  componentDidMount() {
    this.dateUpdateInt = setInterval(() => this.setState({ now: new Date() }), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.dateUpdateInt);
  }

  render() {
    if (this.props.isMobile === false) {
      return (
        <h4 className="m-3">
          {this.handleClass()}
          {this.handleAssignments()}
        </h4>
      );
    } else {
      return (
        <h4 className="mobile-desc">
          {this.handleClass()}
          {this.handleAssignments()}
        </h4>
      );
    }
  }

  handleAssignments = () => {
    const assin = this.props.upcoming;
    let finalL = [];
    for (let i = 0; i < assin.length; i++) {
      if (assin[i].completed === false) {
        finalL.push(assin[i]);
      }
    }

    if (finalL.length === 0) {
      return <div>All tasks completed</div>;
    } else if (finalL.length === 1) {
      return <div><span className="assign-badge lightblue">1</span> task to complete</div>;
    }
    return <div><span className="assign-badge lightblue">{finalL.length}</span> tasks to complete</div>;
  }

  handleClass = () => {
    const date = this.state.now;
    // let nextClass = this.formatClass(this.findNextClass(date))
    // let nextTime = this.findNextTime(date)
    //
    // if (nextClass != null) {
    //   return <div className="p-b-10">{nextClass} in <strong>{nextTime}</strong></div>
    // }

    let nextClass = this.findNext(date);
    if (nextClass != null) {
      return <div className="p-b-10">{this.formatClass(nextClass.name)} in <strong>{nextClass.time}</strong></div>
    }

    return <div className="p-b-10">School's Out for the Day</div>;
  }

  findNext = date => {
    return { name:"School Ends", time:"1 hr 24 min" };
  }

  copy(o) {
    var output, v, key;
    output = Array.isArray(o) ? [] : {};
    for (key in o) {
      v = o[key];
      output[key] = (typeof v === "object") ? this.copy(v) : v;
    }
    return output;
  }

  findNextClass = date => {
    const schedule = this.copy(this.parseSchedule(this.getAlternateSchedule(date), date))
    const timeNow = date.getHours() + ":" + date.getMinutes()

    for (let i in schedule) {
      if (schedule[i].name === "END") {
        return "School Ends";
      }
      if (parseInt(timeNow.split(":")[0], 10) < parseInt(schedule[i].start.split(":")[0], 10)) {
        return schedule[i].name;
      } else if (parseInt(timeNow.split(":")[0], 10) === parseInt(schedule[i].start.split(":")[0], 10)) {
        if (parseInt(timeNow.split(":")[1], 10) <= parseInt(schedule[i].start.split(":")[1], 10)) {
          return schedule[i].name;
        }
      }
    }

    return null;
  }

  findNextTime = date => {
    const schedule = this.copy(this.parseSchedule(this.getAlternateSchedule(date), date))
    const timeNow = date.getHours() + ":" + date.getMinutes()

    for (let i in schedule) {
      let differenceHours = schedule[i].start.split(":")[0] - timeNow.split(":")[0]
      let differenceMinutes = schedule[i].start.split(":")[1] - timeNow.split(":")[1]

      if (differenceMinutes < 0) {
        differenceHours -= 1;
        differenceMinutes = 60 + differenceMinutes;
      }

      let diffString = differenceHours + ":" + differenceMinutes;

      if (differenceHours >= 0) {
        return this.formatTime(diffString);
      }
    }

    return null;
  }

  formatTime = (time) => {
    let hrs = parseInt(time.split(":")[0], 10)
    let mins = parseInt(time.split(":")[1], 10)

    if (hrs === 0) {
      return mins + " min" + this.isPlural(mins);
    } else {
      if (mins === 0) {
        return hrs + " hr" + this.isPlural(hrs);
      } else {
        return hrs + " hr" + this.isPlural(hrs) + " " + mins + " min" + this.isPlural(mins);
      }
    }
  }

  isPlural(num) {
    if (num !== 1) {
      return "s";
    }

    return "";
  }

  formatClass = theClass => {
    if (theClass != null) {
      const classClass = "class-badge " + this.getClassColor(theClass);
      return <span className={classClass}>{theClass}</span>
    }

    return null;
  }

  getClassColor(period) {
    if (Datum.periodToColor[period] != null) {
      return Datum.periodToColor[period];
    } else {
      return Datum.periodToColor["Other"];
    }
  }

  parseSchedule = (sched, date) => {
    if (sched == null) {
      return Datum.normalSchedules[date.getDay()];
    } else {
      let splitten = sched.description.split(")");
      splitten.splice(-1,1);
      let names = splitten.map(x => x.split(" (")[0]);
      let times = splitten.map(x => x.split(" (")[1]);

      times = times.map(x => x.split("-"));

      let convertNext = false;
      for (let x in times) {
        if (convertNext) {
          times[x][0] = String(parseInt(times[x][0].split(":")[0], 10) + 12) + ":" + times[x][0].split(":")[1]
          times[x][1] = String(parseInt(times[x][1].split(":")[0], 10) + 12) + ":" + times[x][1].split(":")[1]
          continue;
        }
        if (parseInt(times[x][0].split(":")[0], 10) > parseInt(times[x][1].split(":")[0], 10)) {
          convertNext = true;
          times[x][1] = String(parseInt(times[x][1].split(":")[0], 10) + 12) + ":" + times[x][1].split(":")[1]
        }
      }

      let i;
      for (i in splitten) {
        splitten[i] = { name:names[i], start:times[i][0], end:times[i][1] };
      }
      return splitten;
    }
  }

  getAlternateSchedule = date => {
    let filteredEvents = this.filterEventsByDate(date);

    return this.findAlternate(filteredEvents);
  }

  findAlternate(events) {
    for (let i in events) {
      for (let x in events[i].summary.split(" ")) {
        if (Datum.keywords.indexOf(events[i].summary.split(" ")[x].toLowerCase()) >= 0) {
          return events[i];
        }
      }
    }

    return null;
  }

  filterEventsByDate = date => {
    let datesEvents = [];
    for (let i = 0; i < this.props.events.length; i++) {
      let startDate = (this.props.events[i].start.date || (this.props.events[i].start.dateTime.split("T"))[0]).split("-");
      startDate = new Date(startDate[0],(startDate[1] - 1),startDate[2]);
      if (this.sameDay(startDate, date)) {
        datesEvents.push(this.props.events[i]);
      }
    }
    return datesEvents;
  }

  sameDay(d1, d2) {
    d1 = new Date(d1);
    d2 = new Date(d2);
    return d1.getFullYear() === d2.getFullYear() && d1.getMonth() === d2.getMonth() && d1.getDate() === d2.getDate();
  }

}

export default TodayDescriptor;
