import React, { Component } from 'react';

class SettingsModal extends Component {
  state = {
    classNames: {
      "Period A":"Period A",
      "Period B":"Period B",
      "Period C":"Period C",
      "Period D":"Period D",
      "Period E":"Period E",
      "Period F":"Period F",
      "Period G":"Period G"
    },
    name: "Anonymous Cow"
  };

  saveSettings = () => {
    this.props.onSave(this.state);
  }

  handleChange = event => {
    const target = event.target;
    if (event.target.id === "inputName") {
      const value = target.value;

      this.setState({ name : value });
    } else {
      const value = target.value;
      const name = target.placeholder;

      let tempClassN = this.state.classNames;
      tempClassN[name] = value;

      this.setState({
        classNames: tempClassN
      });
    }
  }

  render() {
    return (
      <div className="modal fade" id="settingsModal" tabIndex="-1" role="dialog" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Settings</h5>
              <button type="button" className="close exit-button" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>

            <div className="modal-body">
              <div className="container-fluid">
                <form>
                  <div className="row">
                    <div className="col form-group">
                      <label htmlFor="inputPeriodA">Period A</label>
                      <input type="text" className="form-control" id="inputPeriodA" placeholder="Period A" onChange={this.handleChange} />
                    </div>
                    <div className="col form-group">
                      <label htmlFor="inputPeriodB">Period B</label>
                      <input type="text" className="form-control" id="inputPeriodB" placeholder="Period B" onChange={this.handleChange} />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col form-group">
                      <label htmlFor="inputPeriodC">Period C</label>
                      <input type="text" className="form-control" id="inputPeriodC" placeholder="Period C" onChange={this.handleChange} />
                    </div>
                    <div className="col form-group">
                      <label htmlFor="inputPeriodD">Period D</label>
                      <input type="text" className="form-control" id="inputPeriodD" placeholder="Period D" onChange={this.handleChange} />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col form-group">
                      <label htmlFor="inputPeriodE">Period E</label>
                      <input type="text" className="form-control" id="inputPeriodE" placeholder="Period E" onChange={this.handleChange} />
                    </div>
                    <div className="col form-group">
                      <label htmlFor="inputPeriodF">Period F</label>
                      <input type="text" className="form-control" id="inputPeriodF" placeholder="Period F" onChange={this.handleChange} />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col form-group">
                      <label htmlFor="inputPeriodG">Period G</label>
                      <input type="text" className="form-control" id="inputPeriodG" placeholder="Period G" onChange={this.handleChange} />
                    </div>
                    <div className="col form-group">
                      <label htmlFor="inputName">Name</label>
                      <input type="text" className="form-control" id="inputName" placeholder="Anonymous Cow" onChange={this.handleChange} />
                    </div>
                  </div>
                </form>
              </div>
            </div>

            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary" onClick={this.saveSettings} data-dismiss="modal">Save</button>
            </div>
          </div>
        </div>
      </div>
    );
  }

}

export default SettingsModal;
